import React, { Component } from 'react'
import Rave from 'react-flutterwave-rave'

export class BuySell extends Component {
  constructor(props) {
    super(props);
    const userProfile = JSON.parse((localStorage.getItem('users'))).data;
    this.onChangeAmount = this.onChangeAmount.bind(this);
    this.state = {
      key: "FLWPUBK_TEST-6934f1dd2ff0e143bdbc1149a07a976c-X", // RavePay PUBLIC KEY
      phone: userProfile.phone,
      amount: "",
      firstname: userProfile.firstname,
      lastname: userProfile.lastname,
      email: userProfile.email,
      transaction_description: "Buying Bitcoin",
      txref: (Math.floor(Math.random() * 10)) + (new Date()).getMilliseconds(),
      bitcoin_address: ""
    }

    this.callback = this.callback.bind(this);
    this.close = this.close.bind(this);
  }
  callback = (response) => {
    console.log(response);
    alert("Payment Succesfull");
  }
  onChangeAmount(e) {
    this.setState({
      amount: e.target.value
    });
  }
  close = () => {
    console.log("Payment closed");
    alert("Payment Closed");
  }
  render() {

    return (
      <div>
        <div className="content-body">
          <div className="container-fluid">
            <div className="row">
              <div className="col-xl-5 col-lg-5 col-md-12">
                <div className="card">
                  <div className="card-body">
                    <div className="buy-sell-widget">
                      <div className="card">
                        <h4 className="card-title">Sell Bitcoin </h4>
                      </div>
                      {/* <ul className="nav nav-tabs">
                        <li className="nav-item"><Link className="nav-link active" data-toggle="tab" to="#buy">Buy</Link>
                        </li> */}
                      {/* <li className="nav-item"><Link className="nav-link" data-toggle="tab" to="#sell">Sell</Link> */}
                      {/* </li>
                      </ul> */}
                      <div className="tab-content tab-content-default">
                        <div className="tab-pane fade show active" id="buy" role="tabpanel">

                          <form name="myform" className="currency_validate">

                            <div className="form-group">
                              <div className="input-group mb-3">
                                <select name="currency" className="form-control">
                                  <option data-display="Bitcoin" value="bitcoin">Bitcoin
                                  </option>
                                </select>
                                <input type="text" name="amount" id="amount" className="form-control" value={this.state.amount}
                                  onChange={this.onChangeAmount} placeholder="	&#8358; 0.00" />
                              </div>
                            </div>

                            <div className="form-group">
                              <label className="mr-sm-2">Payment Method</label>
                              <div className="input-group mb-3">
                                <select name="currency" className="form-control">
                                  <option data-display="Bitcoin" value="bitcoin" >Card Payment</option>
                                </select>
                              </div>
                            </div>

                            <div className="form-group">
                              <label className="mr-sm-2">Bitcoin Wallet Adress</label>
                              <div className="input-group mb-3">
                                <input type="text" name="usd_amount" className="form-control" placeholder="1BoatSLRHtKNngkdXEeobR76b53LETtpyT" />
                              </div>
                            </div>

                            {/* <div className="form-group">
                              <label className="mr-sm-2">Beneficiary Account </label>
                              <div className="input-group mb-3">
                                <select name="currency" className="form-control">
                                  <option data-display="Bitcoin" value="bitcoin">Bank </option>
                                  <option value="litecoin">Litecoin</option>
                                </select>
                                <input type="text" name="usd_amount" className="form-control" defaultValue="0000234565" />
                              </div>
                            </div> */}
                          </form>
                        </div>


                        {/* <div className="tab-pane fade" id="sell">
                          <form name="myform" className="currency2_validate">
                            <div className="form-group">
                              <label className="mr-sm-2">Currency</label>
                              <div className="input-group mb-3">
                                <select name="currency" className="form-control">
                                  <option data-display="Bitcoin" value="bitcoin">Bitcoin
                          </option>
                                  <option value="litecoin">Litecoin</option>
                                </select>
                                <input type="text" name="usd_amount" className="form-control" defaultValue="125.00 USD" />
                              </div>
                            </div>
                            <div className="form-group">
                              <label className="mr-sm-2">Payment Method</label>
                              <div className="input-group mb-3">
                                <select name="currency" className="form-control">
                                  <option data-display="Bitcoin" value="bitcoin">Bitcoin
                          </option>
                                  <option value="litecoin">Litecoin</option>
                                </select>
                                <input type="text" name="usd_amount" className="form-control" defaultValue="125.00 USD" />
                              </div>
                            </div>
                            <div className="form-group">
                              <label className="mr-sm-2">Enter your amount</label>
                              <div className="input-group">
                                <input type="text" name="currency_amount" className="form-control" placeholder="0.0214 BTC" />
                                <input type="text" name="usd_amount" className="form-control" placeholder="125.00 USD" />
                              </div>
                              <div className="d-flex justify-content-between mt-3">
                                <p className="mb-0">Monthly Limit</p>
                                <h6 className="mb-0">$49750 remaining</h6>
                              </div>
                            </div>
                          </form> */}

                        {/* </div> */}


                        <p className="p-4">Note: Exchanges provide highly varying degrees of safety, security, privacy, and control over your funds and information. Perform your own due diligence
         and choose a wallet where you will keep your bitcoin before selecting an exchange.</p>
                      </div>
                    </div>
                  </div>


                </div>
              </div>






              <div className="col-xl-7 col-lg-7 col-md-12">
                <div className="card">
                  <div className="card-body">
                    <div className="buyer-seller">

                      <div className="table-responsive">
                        <table className="table">
                          <tbody>
                            <tr>
                              <td><span className="text-primary">You are Buying</span></td>
                              <td><span className="text-primary">0.00254 BTC</span></td>
                            </tr>
                            <tr>
                              <td>Payment Method</td>
                              <td>Card Payment</td>
                            </tr>
                            <tr>
                              <td>Exchange Rate</td>
                              <td>0.00212455 BTC</td>
                            </tr>
                            <tr>
                              <td>Fee</td>
                              <td>$28.00 USD</td>
                            </tr>
                            <tr>
                              <td>Total</td>
                              <td>$854.00 USD</td>
                            </tr>
                            <tr>
                              <td>Vat</td>
                              <td>
                                <div className="text-danger">$25.00 USD</div>
                              </td>
                            </tr>
                            <tr>
                              <td> Sub Total</td>
                              <td> $1232.00 USD</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>

                  </div>
                  {/* <button name="submit" className="btn btn-success btn-block">Exchange  Now</button> */}
                  <Rave
                    pay_button_text="Exchange  Now"
                    class="btn btn-success btn-block"
                    metadata={[
                      { metaname: 'transaction_description', metavalue: this.state.transaction_description }
                    ]}
                    payment_method="card"
                    txref={this.state.txref + ""}
                    customer_email={this.state.email}
                    customer_phone={this.state.phone}
                    amount={"" + this.state.amount}
                    ravePubKey={this.state.key}
                    callback={this.callback}
                    onclose={this.close}
                  />
                </div>
              </div>

            </div>
          </div>
        </div >
      </div >
    )
  }
}

export default BuySell
