import React, { Component } from 'react'

export class Verify6 extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/dashboard')
  }
    render() {
        return (
            <div>
                
                <div className="content-body">
    <div className="container-fluid">
    <div className="verification section-padding">
  <div className="container h-100">
    <div className="row justify-content-center align-items-center">
      <div className="col-xl-5 col-md-6">
        <div className="auth-form card">
          <div className="card-body">
            <form onSubmit={this.onSubmitHandler} className="identity-upload">
              <div className="identity-content">
                <span className="icon"><i className="fa fa-check" /></span>
                <h4>Congratulation. Your bank details added</h4>
              </div>
              <div className="text-center">
                <button type="submit" className="btn btn-success pl-5 pr-5">Continue</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</div>
</div>

            </div>
        )
    }
}

export default Verify6
