import React, { Component } from 'react';
import { HashRouter, Switch } from 'react-router-dom';
import Home from './pages/Home';
import Login from './pages/Login';
import Registration from './pages/Registration'; 
import About from './pages/About';
import Price from './pages/Price';
import Privacy from './pages/Privacy';
import Otp1 from './LoginVerify/Otp1';
import Otp2 from './LoginVerify/Otp2';
import Reset from './LoginVerify/Reset';
import Lock from './LoginVerify/Lock';
import AppRoute from './RouteConfg/AppRoute';
import MainLayout from './Layout/MainLayout';
import DashboardLayout from './Layout/DashboardLayout';
import Dashboard from './Users/Dashboard';
import BuySell from './Users/BuySell';
import Settings from './Users/Settings';
import AccountSetting from './Users/AccountSetting';
import Security from './Users/Security';
import AddBank from './Users/AddBank';
import Verify4 from './Users/Verify4';
import Verify6 from './Users/Verify6';
import Historys from './Users/Historys';
import Loginlayout from './Layout/Loginlayout';
import Contact from './pages/Contact';
import ChangePassword from './Users/ChangePassword';

class App extends Component { 


   render() {

  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
    <Switch>
        <AppRoute exact path="/" component={Home} layout={MainLayout} />
        <AppRoute path="/price" component={Price} layout={MainLayout} />
        <AppRoute path="/about" component={About} layout={MainLayout} />
        <AppRoute path="/policy" component={Privacy} layout={MainLayout}/>
        <AppRoute path="/contact" component={Contact} layout={MainLayout}/>
        <AppRoute path="/otp1" component={Otp1} layout={Loginlayout}/>
        <AppRoute path="/otp2" component={Otp2} layout={Loginlayout}/>
        <AppRoute path="/reset" component={Reset} layout={Loginlayout}/>
        <AppRoute path="/login" component={Login} layout={Loginlayout}/>
        <AppRoute path="/register" component={Registration} layout={Loginlayout}/>
        <AppRoute path="/lock" component={Lock} layout={Loginlayout}/>
        {/* Dashboard Links */}
       
        <AppRoute path="/dashboard" component={Dashboard} layout={DashboardLayout}/>
        <AppRoute path="/buySell" component={BuySell} layout={DashboardLayout}/>
        <AppRoute path="/settings" component={Settings} layout={DashboardLayout}/>
        <AppRoute path="/accountSetting" component={AccountSetting} layout={DashboardLayout}/>
        <AppRoute path="/security" component={Security} layout={DashboardLayout}/>
        <AppRoute path="/addBank" component={AddBank} layout={DashboardLayout}/>
        <AppRoute path="/historys" component={Historys} layout={DashboardLayout}/>
        <AppRoute path="/verify4" component={Verify4} layout={DashboardLayout} />
        <AppRoute path="/addBankSucces" component={Verify6} layout={DashboardLayout} />     
        <AppRoute path="/changePassword" component={ChangePassword} layout={DashboardLayout} />

    </Switch>
    </HashRouter>  
    
  );
}
}

export default App;
