import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import Form from "react-validation/build/form";
import AuthService from "../Services/auth.service";

const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        This is not a valid email.
      </div>
    );
  }
};

const vpassword = value => {
  if (value.length < 6 || value.length > 40) {
    return (
      <div className="alert alert-danger" role="alert">
       The Password must be at least 6 characters long..
      </div>
    );
  }
};

const conpass = val => {
 
  var password = document.getElementById("password1").value;
        var confirmPassword = document.getElementById("password2").value;
        if (password !== confirmPassword) {
          return (
            <div className="alert alert-danger" role="alert">
              Passwords do not match.   
            </div>
          );
        }
        
};



export class Registration extends Component {

  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);        
    this.onChangeFirstname = this.onChangeFirstname.bind(this);
    this.onChangeLastname = this.onChangeLastname.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePhone = this.onChangePhone.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.onChangeCpassword = this.onChangeCpassword.bind(this);

    this.state = {
      first_name:"",
      last_name:"",
      email: "",
      phone:"",
      password: "",
      c_password:"",
      successful: false,
      loading: false,
      message: ""
    };
  }

    onChangeFirstname(e) {
      this.setState({
        first_name: e.target.value
      });
    }
    onChangeLastname(e) {
      this.setState({
        last_name: e.target.value
      });
    }

  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }
  onChangePhone(e) {
    this.setState({
      phone: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  onChangeCpassword(e) {
    this.setState({
      c_password: e.target.value
    });
  }

  handleRegister(e) {
    e.preventDefault();

    this.setState({
      message: "",
      loading: true,
      successful: false
    });

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      const regmessage = "Already registered. Go to Sign up";
      AuthService.register(
        this.state.first_name,
        this.state.last_name,
        this.state.email,
        this.state.phone,
        this.state.password,
        this.state.c_password
      ).then(
        response => {
          this.setState({
            message: regmessage,
            successful: true,
            loading: true


          });
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage,
            loading: false
          });
        }
      );
    }
  }
 

  render() {
        return (
  <div> 
  
  <div className="container ">
    <div className="row justify-content-center align-items-center">
      <div className="col-xl-5 col-md-6">
      <div className="mini-logo text-center my-5">
      <Link  to="/"> <img src="assets/images/blogo.jpg" 
      alt="" style={{width:"200px"}}/></Link>
      </div>
        <div className="auth-form card">
          <div className="card-header justify-content-center">
            <h4 className="card-title">Sign up your account</h4>
          </div>
          <div className="card-body">
            <Form onSubmit={this.handleRegister} ref={c => { this.form = c; }} >

            {!this.state.successful && (
            <div className="container">

              <div className="row justify-content-between align-items-center">
                 <div className="col-xl-6 col-lg-6 col-12">             
                <div className="form-group">
                  <label htmlFor="first_name">Firstname</label>
                  <Input type="text" className="form-control" name="first_name" value={this.state.first_name}
                    onChange={this.onChangeFirstname} validations={[required]}  placeholder="Enter First Name"/>
                    </div>
                    </div>

                <div className="col-xl-6 col-lg-6 col-12">
                 <div className="form-group">
                  <label htmlFor="last_name">Lastname</label>
                  <Input type="text" className="form-control" name="last_name" value={this.state.last_name}
                    onChange={this.onChangeLastname} validations={[required]} placeholder="Enter Last Name" />
                </div>
              </div>
              </div>
               
              <div className="form-group">
                  <label htmlFor="phone">Phone Number</label>
              <Input type="tel" className="form-control" name="phone" value={this.state.phone}
              onChange={this.onChangePhone} validations={[required]}  placeholder="Enter Phone Number" />
              </div>
              
              <div className="form-group">
              <label htmlFor="email">Email</label>
              <Input type="text" className="form-control" name="email" value={this.state.email}
               onChange={this.onChangeEmail} validations={[required, email]} placeholder="Enter Valid Email"/>
            </div>

              <div className="form-group">
                  <label htmlFor="password">Password</label>
                  <Input type="password" className="form-control" id="password1" name="password" value={this.state.password}
                  onChange={this.onChangePassword} validations={[vpassword]} placeholder="Enter Password"/>
                </div>
                

            <div className="form-group">
            <label htmlFor="c_password"> Confirm Password</label>
             <Input type="password" className="form-control" name="c_password" id="password2" value={this.state.c_password}
               onChange={this.onChangeCpassword} validations={[required, vpassword, conpass]} placeholder="Re-enter Password"/>
                 </div>
                

                <div className="text-center mt-4">
                <button className="btn btn-success btn-block" disabled={this.state.loading}>
                 {this.state.loading && (
                  <span className="spinner-border spinner-border-sm"></span>)}
                   <span>Sign in</span>
                    </button>
              </div>
            </div> )}
                {this.state.message && (
              <div className="form-group">
                <div className={ this.state.successful ? "alert alert-success" : "alert alert-danger"}
                  role="alert"> {this.state.message}
                </div>
              </div>)}
            <CheckButton style={{ display: "none" }} ref={c => { this.checkBtn = c; }}/>
            </Form>
            <div className="new-account mt-3">
              <p>Already have an account? <Link className="text-primary" to="/login">Sign up</Link>
              </p>
            </div>
          </div>
       </div>
      </div>
      </div>
</div>   
</div>


        )
    }
}

export default Registration
