import React, { Component } from 'react'
// import {Link} from 'react-router-dom'

export class About extends Component {
    render() {
        return (
            
                <div>
                    {/* start */}
                    <div className="about-one section-padding">
                        <div className="container">
                            <div className="row align-items-center">
                                <div className="col-lg-6">
                                    <div className="service-img">
                                        <img src="assets/images/about/3.jpg" alt="" className="img-fluid" />
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <div className="service-content m-t-50">
                                        <h2>In Brief</h2>
                                        <p>Founded in January of 2020, TradeCoins is a digital currency wallet and platform where
                                        merchants
                                        and consumers can transact with new digital currencies like bitcoin, ethereum, and
                                        litecoin. We're based in San Francisco, California.</p>
                                        <p>Bitcoin is the world's most widely used alternative currency with a total market cap of
                                        over
                                        $100 billion. The bitcoin network is made up of thousands of computers run by
                                        individuals all over the world.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* end */}

                    {/* start */}
                    <div className="our-ceo py-5">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="ceo-content">
                                        <div className="media">
                                            <img src="assets/images/avatar/6.jpg" alt="" className="img-fluid mr-4 rounded-circle" />
                                            <div className="media-body">
                                                <h3>Eric Benz</h3>
                                                <span>CEO of Tradix</span>
                                                <p className="mt-2">Eric Benz has over 10 years of experience working in and around
                                                Financial Technology. He has delivered innovative SaaS systems for some of
                                                today's biggest institutions around payments, identity, and banking
                                                infrastructure. John Abraham has been in the Blockchain space since 2012 and is
                                                involved in a number of blockchain and fintech businesses both as an investor,board director, and founder.</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* end */}

                </div>

           
        )
    }
}

export default About
