import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import AuthService from "../Services/auth.service";

export class AddBank extends Component {
  // onSubmitHandler = (e) =>{
  //   e.preventDefault();
  //   this.props.history.push('/verify6')
  // }
  constructor(props) {
    super(props);
    this.handleAdd = this.handleAdd.bind(this);        
    this.onChangeaccNum = this.onChangeaccNum.bind(this);
    this.onChangeaccName = this.onChangeaccName.bind(this);
    this.onChangeBank = this.onChangeBank.bind(this);
    
    //const userProfile = JSON.parse((localStorage.getItem('users'))).data;  
    this.state = {
      account_number:"",
      account_name:"",
      bank_name: "",
      successful: false,
      message: ""
    };
  }

  onChangeaccNum(e) {
    this.setState({
      account_number: e.target.value
    });
  }
  onChangeaccName(e) {
    this.setState({
      account_name: e.target.value
    });
  }

onChangeBank(e) {
  this.setState({
    bank_name: e.target.value
  });
}

handleAdd(e) {
  e.preventDefault();

  this.setState({
    account_number:"",
    account_name:"",
    bank_name: "",
    loading:true,
    message: "",
    successful: false
   });


  //if (this.checkBtn.context._errors.length === 0) 
  const regmessage = "Bank added Successfully!!!";
    AuthService.postAddBank(    
      this.state.account_number,
      this.state.account_name,
      this.state.bank_name
    ).then(
      response => {
        this.setState({
          message:regmessage,
          successful: true,
          loading:false
        }); 
       // this.props.history.push("/addBankSucces");
       // window.location.reload();
        
      },
      error => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
         successful: false,
         message: resMessage,
         loading:false
        });
      }
    );
  
}

    render() {
        return (
            <div>

<div className="content-body">
    <div className="container-fluid">
<div className="verification section-padding mb-5">
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center  my-5">
      <div className="col-xl-5 col-md-6">
        <div className="auth-form card">
          <div className="card-header">
            <h4 className="card-title">Link bank account</h4>
          </div>
          <div className="card-body">
            <form onSubmit={this.handleAdd}  className="identity-upload">
              <div className="form-row">
                <div className="form-group col-xl-12">
                  <label className="mr-sm-2">Account Name </label>
                  <input type="text" className="form-control" placeholder="Maria Pascle"
                   value={this.state.account_name} onChange={this.onChangeaccName}  required/>
                </div>
                <div className="form-group col-xl-12">
                  <label className="mr-sm-2">Account number </label>
                  <input type="text" className="form-control" placeholder={3647545678} 
                  value={this.state.account_number} onChange={this.onChangeaccNum} required/>
                </div>
                <div className="form-group col-xl-12">
                  <label className="mr-sm-2">Bank Name </label>
                  <input type="text" className="form-control" placeholder="Bank Of Africa"
                  value={this.state.bank_name} onChange={this.onChangeBank} required />
                </div>
                <div className="text-center col-12">
                  <Link to="/accountSetting" className="btn btn-primary mx-2">Back</Link>

                  <button type="submit" className="btn btn-success mx-2" disabled={this.state.loading}>
                  {this.state.loading && (
                  <span className="spinner-border spinner-border-sm"></span>)}
                   <span>Save</span></button>

                </div>
              </div>
              {this.state.message && (
              <div className="form-group">
                <div className={ this.state.successful ? "alert alert-success" : "alert alert-danger"}
                  role="alert"> {this.state.message}
                </div>
              </div>)}
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</div>
</div>



            </div>
        )
    }
}

export default AddBank
