import React, { Component } from 'react'
import { Link } from 'react-router-dom'
export class Reset extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/login')
  }
    render() {
        return (
         <div>
  <div className="authincation section-padding">
    <div className="container h-100">
      <div className="row justify-content-center h-100 align-items-center">
        <div className="col-xl-5 col-md-6">
          <div className="auth-form card">
            <div className="card-header justify-content-center">
              <h4 className="card-title">Reset password</h4>
            </div>
            <div className="card-body">
              <form onSubmit={this.onSubmitHandler}>
                <div className="form-group">
                  <label>Email</label>
                  <input type="email" className="form-control" defaultValue="hello@example.com" />
                </div>
                <div className="text-center">
                  <button type="submit" className="btn btn-success btn-block">Get New Password</button>
                </div>
              </form>
              <div className="new-account mt-3">
                <p className="mb-1">Don't Received? </p>
                <Link to="/reset" className="text-primary" >Resend</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>


        )
    }
}

export default Reset
