import React from 'react';
import { Link, NavLink } from 'react-router-dom';


const Header = () => {
  return (
    <div>
      {/* Header Section */}
      <div className="header">
        <div className="container">
          <div className="row">
            <div className="col-xl-12">
              <div className="navigation">
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <Link className="navbar-brand" to="/"><img src="assets/images/blogo.jpg" alt=""/></Link>

                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon" />
                  </button>
                  <div className="collapse navbar-collapse" id="navbarNavDropdown">
                    <ul className="navbar-nav">
                      <li className="nav-item">
                        <Link className="nav-link" to="/">Home</Link>
                      </li>
                      <li className="nav-item">
                        <NavLink className="nav-link" to="/about">AboutUs</NavLink>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link" to="/contact">ContactUs</Link>
                      </li>
                    </ul>
                  </div>
                  <div className="signin-btn">
                    <Link className=" btn btn-primary" to="/login">SignIn</Link>
                  </div>
                </nav>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Header
