import axios from "axios";
//import authHeader from './auth-header';

const API_URL = "https://cryptong.herokuapp.com/api/";


class AuthService {
  login(email, password) {
    return axios
      .post(API_URL + "login", {
        email,
        password
      })
      .then(response => {
        console.log(response.data);
        if (response.data) {         
           localStorage.setItem("access_token", JSON.stringify(response.data));
        }

        return response.data;
       
      });
  }

  logout() {
    localStorage.removeItem("users");
    localStorage.removeItem("access_token");
  }
 
  register(first_name,last_name, email,phone, password,c_password) {
    return axios.post(API_URL + "register", {
     first_name,
     last_name,
      email,
      phone,
      password,
      c_password
    });
  }

  getCurrentUser() {   
    return JSON.parse(localStorage.getItem('access_token'));
  }

  getProfile(){
 // const headers = authHeader();
  const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
  const headers = { Authorization: 'Bearer ' + token };
  console.log(headers);
  return axios.get(API_URL + 'profile', { headers: headers  }).then((res)=>{
    localStorage.setItem("users", JSON.stringify(res.data));
    return true;
  }).catch(err=>{
    console.log(err);
    return false;
  });
  }


  postUpdateProfile(date_of_birth,bvn,address) {
    const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
    const headers = {Authorization: 'Bearer ' + token };
    console.log(headers);
    return axios.post(API_URL + 'profile',{ date_of_birth,bvn,address},{ headers: headers}).then(result => {
      console.log(result);
    }).catch(err => {
      console.log(err);
    });
  }


  postUpdatePassword(current_password,new_password,new_confirm_password) {
    const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
    const headers = {Authorization: 'Bearer ' + token };
    console.log(headers);
    return axios.post(API_URL + 'change-password',{current_password,new_password,new_confirm_password},{ headers: headers}).then(result => {
      console.log(result);
    }).catch(err => {
      console.log(err);
    });
  }

  postAddBank(account_number,account_name,bank_name) {
    const token = (JSON.parse(localStorage.getItem('access_token'))).data.access_token;
    const headers = {Authorization: 'Bearer ' + token };
    console.log(headers);
    return axios.post(API_URL + 'add-bank',{ account_number,account_name,bank_name},{ headers: headers}).then(result => {
      console.log(result);
    }).catch(err => {
      console.log(err);
    });
  }
  
}

export default new AuthService();