import React, { Component } from 'react'

export class Verify4 extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/addBank')
  }
    render() {
        return (
            <div>
<div className="verification section-padding">
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center">
      <div className="col-xl-5 col-md-6">
        <div className="auth-form card">
          <div className="card-body">
            <form onSubmit={this.onSubmitHandler} className="identity-upload">
              <div className="identity-content">
                <span className="icon"><i className="fa fa-check" /></span>
                <h4>Identity Verified</h4>
                <p>Congrats! your identity has been successfully verified and your investment
                  limit has been increased.</p>
              </div>
              <div className="text-center mb-4">
                <button type="submit" className="btn btn-success pl-5 pr-5">Continue</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>




            </div>
        )
    }
}

export default Verify4
