import React, { Component } from 'react'

export class Otp2 extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/security')
  }
    render() {
        return (
            <div>
            <div className="authincation section-padding">
  <div className="container h-100">
    <div className="row justify-content-center h-100 align-items-center">
      <div className="col-xl-5 col-md-6">
        <div className="auth-form card">
          <div className="card-body">
            <a className="page-back text-muted" href="otp-1.html"><span><i className="fa fa-angle-left" /></span> Back</a>
            <h3 className="text-center">OTP Verification</h3>
            <p className="text-center mb-5">We will send one time code on this number</p>
            <form onSubmit={this.onSubmitHandler}>
              <div className="form-group">
                <label>Your OTP Code</label>
                <input type="text" className="form-control text-center font-weight-bold" defaultValue="11 22 33" />
              </div>
              <div className="text-center">
                <button type="submit" className="btn btn-success btn-block">Verify</button>
              </div>
            </form>
            <div className="info mt-3">
              <p className="text-muted">You dont recommended to save password to browsers!</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
  
            </div>
        )
    }
}

export default Otp2
