import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Security extends Component {
  onSubmitHandler = (e) =>{
    e.preventDefault();
    this.props.history.push('/otp2')
  }
    render() {
        return (
            <div>
                
 <div className="content-body">
  <div className="container-fluid">
    <div className="row">
      <div className="col-xl-3 col-md-4">
        <div className="card settings_menu">
          <div className="card-header">
            <h4 className="card-title">Settings</h4>
          </div>
          <div className="card-body">
            <ul>
              <li className="nav-item">
                <Link to="/settings" className="nav-link">
                  <i className="la la-user" />
                  <span>Update Profile</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/security" className="nav-link active">
                  <i className="la la-lock" />
                  <span>Security</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/accountSetting" className="nav-link">
                  <i className="la la-university" />
                  <span>Link Bank Account</span>
                </Link>
              </li>

            </ul>
          </div>
        </div>
      </div>
      <div className="col-xl-9 col-md-8">
        <div className="card">
          <div className="card-header">
            <h4 className="card-title">Security</h4>
          </div>
          <div className="card-body">
            <div className="row align-items-center">
              <div className="col-xl-4">
                <div className="id_card">
                  <img src="assets/images/id.png" alt="" className="img-fluid" />
                </div>
              </div>
              <div className="col-xl-6">
                <div className="id_info">
                  <h3>Maria Pascle </h3>
                  <p className="mb-1 mt-3">ID: 0024 5687 2254 3698 </p>
                  <p className="mb-1">Status: <span className="font-weight-bold">Verified</span></p>
                  <Link to="/verify2" className="btn btn-success mt-3">New ID</Link>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-xl-12">
                <div className="phone_verify">
                  <h4 className="card-title mb-3">Email Address</h4>
                  <form onSubmit={this.onSubmitHandler}>
                    <div className="form-row align-items-center">
                      <div className="form-group col-xl-6">
                        <input type="text" className="form-control" placeholder="hello@example.com " />
                        <button className="btn btn-success mt-4">Add</button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
              <div className="col-xl-12">
                <div className="phone_verified">
                  <h5> <span><i className="fa fa-envelope" /></span> hello@example.com</h5>
                  <div className="verify">
                    <div className="verified">
                      <span><i className="la la-check" /></span>
                      <Link to="#">Verified</Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col-xl-12">
                <div className="phone_verify">
                  <h4 className="card-title mb-3">Add Phone Number</h4>
                  <form onSubmit={this.onSubmitHandler} >
                    <div className="form-row align-items-center">
                      <div className="form-group col-xl-6">
                        <input type="text" className="form-control" placeholder="+1 2335 2458 " />
                        <button className="btn btn-success mt-4">Add</button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
              <div className="col-xl-12">
                <div className="phone_verified">
                  <h5> <span><i className="fa fa-phone" /></span> +1 23584 2659</h5>
                  <div className="verify">
                    <div className="verified">
                      <span><i className="la la-check" /></span>
                      <Link to="#">Verified</Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>

            </div>
        )
    }
}

export default Security
