import React, { Component } from 'react'
import { Link} from 'react-router-dom';

export class Footer extends Component {
    render() {
        return (
<div>


  {/* Footer Section */}
  <div className="footer">
    <div className="container">
      <div className="row">
        <div className="col-xl-6">
          <div className="copyright">
            <p> Copyright 2020 <Link to="/policy"style={{color:"#FDC727"}}>Blink </Link>
             Powered By<Link to="www.doshservices.com" style={{color:"#FDC727"}}> DoshServices</Link></p>
          </div>
        </div>
        <div className="col-xl-6">
          <div className="footer-social">
            <ul>
              <li><Link to="#"><i className="fa fa-facebook" /></Link ></li>
              <li><Link to="#"><i className="fa fa-twitter" /></Link ></li>
              <li><Link  to="#"><i className="fa fa-linkedin" /></Link ></li>
              <li><Link  to="#"><i className="fa fa-youtube" /></Link ></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</div> 
        
        )
    }
}

export default Footer








