import React, { Component } from 'react'
import { Link } from 'react-router-dom'
//import AuthService from "../Services/auth.service";


export class Dashboard extends Component {

    render() {
  
        return (
          
<div>
<div className="content-body">
  <div className="container-fluid">
    <div className="row">


      {/*Selling and buying Rates  */}
      <div className="col-xl-3 col-lg-3 col-xxl-3">
        <div className="card balance-widget">
          <div className="card-header border-0 py-0">
          <div className="card">
            <h4 className="card-title">Selling Rate </h4>
          </div>
          </div>
              <ul className="list-unstyled">
                <li className="media">
                  <i className="cc BTC mr-3" />
                  <div className="media-body">
                    <h5 className="m-0">Bitcoin</h5>
                  </div>
                  <div className="text-right">
                    <h5>0.000242 BTC</h5>
                    <span>0.125 USD</span>
                  </div>
                </li>
                <div className="card-header border-0 py-0">
                <div className="card">
                <h4 className="card-title">Buying Rate </h4>
                  </div>
                  </div>
                <li className="media">
                  <i className="cc BTC mr-3" />
                  <div className="media-body">
                    <h5 className="m-0">Bitcoin</h5>
                  </div>
                  <div className="text-right">
                    <h5>0.000242 BTC</h5>
                    <span>0.125 USD</span>
                  </div>
                </li>
               
              </ul>
            </div>
          </div>

      <div className="col-xl-3 col-lg-3 col-xxl-9">
      <div className="card balance-widget">
       <div className="card-header border-0 py-0">
         
        <h4 className="card-title">Recent Activities</h4>
      
      </div>
      <div className="card-body">
        <div className="transaction-table">
          <div className="table-responsive">
            <table className="table mb-table-responsive-sm">
              <tbody>
                <tr>
                  <td>
                    <span className="badge badge-danger">Sold</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td>
                    Using - Bank *******5264
                  </td>
                  <td className="text-danger">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>

                <tr>
                  <td>
                    <span className="badge badge-success">Buy</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td>
                    Using - Card *******8475
                  </td>
                  <td className="text-success">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>

                <tr>
                  <td>
                    <span className="badge badge-danger">Sold</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td>
                    Using - Card *******8475
                  </td>
                  <td className="text-danger">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>

                <tr>
                  <td>
                    <span className="badge badge-success">Buy</span>
                  </td>
                  <td>
                    <i className="cc BTC" /> Bitcoin
                  </td>
                  <td>
                    Using - Card *******2321
                  </td>
                  <td className="text-success">-0.000242 BTC</td>
                  <td>-0.125 USD</td>
                </tr>
              </tbody>
            </table>
            <Link to="#" style={{float:"left"}}>View More </Link>
          </div>
          
        </div>
      </div>
        </div>
    </div>
 </div>
  </div>
  </div>
  </div>
        )
    }
}

export default Dashboard
