import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class Sidebar extends Component {
    render() {
        return (
            <div>
<div className="sidebar">
  <div className="menu">
    <ul>
      <li>
        <Link to="/dashboard" data-toggle="tooltip" data-placement="right" title="Home">
          <span><i className="la la-home" /></span>
        </Link>
        
      </li>
      <li>
        <Link to="/buySell" data-toggle="tooltip" data-placement="right" title="Exchange">
          <span><i className="la la-exchange" /></span>
        </Link>
      </li>
      <li>
        <Link to="/settings" data-toggle="tooltip" data-placement="right" title="Settings">
          <span><i className="la la-tools" /></span>
        </Link>
      </li>
    </ul>
  </div>
</div>

            </div>
        )
    }
}

export default Sidebar
