import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import AuthService from "../Services/auth.service";

export class Settings extends Component {

  constructor(props) {
    super(props);
    this.handleUpdate = this.handleUpdate.bind(this);        
    this.onChangeDob = this.onChangeDob.bind(this);
    this.onChangeUbvn = this.onChangeUbvn.bind(this);
    this.onChangeAddress = this.onChangeAddress.bind(this);
    
    const userProfile = JSON.parse((localStorage.getItem('users'))).data;  
    this.state = {
      first_name: userProfile.first_name,
      last_name: userProfile.last_name,
      email: userProfile.email,
      bvn:"",
      date_of_birth:"",
      address: "",
      phone:userProfile.phone,
      successful: false,
      message: ""
    };
  }

  onChangeDob(e) {
    this.setState({
      date_of_birth: e.target.value
    });
  }
  onChangeUbvn(e) {
    this.setState({
      bvn: e.target.value
    });
  }

onChangeAddress(e) {
  this.setState({
    address: e.target.value
  });
}

handleUpdate(e) {
  e.preventDefault();

  this.setState({
    bvn:"",
    date_of_birth:"",
    address: "",
    loading:true,
    message: "",
    successful: false
   });


  //if (this.checkBtn.context._errors.length === 0) 
  const regmessage = "Profile Updated Successfully!!!";
    AuthService.postUpdateProfile(    
      this.state.date_of_birth,
      this.state.bvn,
      this.state.address,
      this.state.phone

    ).then(
      response => {
        this.setState({
          message:regmessage,
          successful: true,
          loading:false
        });
      },
      error => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
         successful: false,
         message: resMessage,
         loading:false
        });
      }
    );
  
}
    render() {
        return (
           
  <div className="content-body">
    <div className="container-fluid">
      <div className="row">
        <div className="col-xl-3 col-md-4">
          <div className="card settings_menu">
            <div className="card-header">
              <h4 className="card-title">Settings</h4>
            </div>
            <div className="card-body">
              <ul>
                <li className="nav-item">
                  <Link to="/settings" className="nav-link active">
                    <i className="la la-user" />
                    <span>Update Profile</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/changePassword" className="nav-link">
                    <i className="la la-lock" />
                    <span>Change Password</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/accountSetting" className="nav-link">
                    <i className="la la-university" />
                    <span>Link Bank Account </span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-xl-9 col-md-8">
          <div className="row">
            
            <div className="col-xl-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="card-title">Update Personal Information</h4>
                </div>
                <div className="card-body">

                  <form onSubmit={this.handleUpdate} className="personal_validate">
                    <div className="form-row">
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">First Name</label>
                        <input type="text" className="form-control" id="first_name" value={this.state.first_name} readOnly/>
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Last Name</label>
                        <input type="text" className="form-control" id="last_name" value={this.state.last_name} readOnly />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Email</label>
                        <input type="email" className="form-control" value={this.state.email} id="email" readOnly />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Date of birth</label>
                        <input type="text" className="form-control" placeholder="10/10/2019" id="date_of_birth"
                         required   value={this.state.date_of_birth} onChange={this.onChangeDob}/>
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Bvn</label>
                        <input type="number" className="form-control" placeholder="563456789" id="bvn"
                         required  value={this.state.bvn} onChange={this.onChangeUbvn} />
                      </div>
                      <div className="form-group col-xl-6">
                        <label className="mr-sm-2">Address</label>
                        <input type="text" className="form-control" placeholder="123, Central Square, Brooklyn" 
                        id="address" required  value={this.state.address} onChange={this.onChangeAddress} />
                      </div>
                      
                    </div>
                    <div className="row">
                      <div className="form-group col-2">
                        <button className="btn btn-success pl-5 pr-5" disabled={this.state.loading}>
                           {this.state.loading && (
                  <span className="spinner-border spinner-border-sm"></span>)}
                   <span>Update</span></button>
                        
                      </div>
                     
                      </div>
                {this.state.message && (
              <div className="form-group">
                <div className={ this.state.successful ? "alert alert-success" : "alert alert-danger"}
                  role="alert"> {this.state.message}
                </div>
              </div>)}
          
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
  
    }
        }
  
export default Settings;
