import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class opt_1 extends Component {
    onSubmitHandler = (e) =>{
        e.preventDefault();
        this.props.history.push('/otp2')
      }
    render() {
        return (
            <div>
                <div className="authincation section-padding">
                    <div className="container h-100">
                        <div className="row justify-content-center h-100 align-items-center">
                            <div className="col-xl-5 col-md-6">
                                <div className="auth-form card">
                                    <div className="card-body">
                                        <Link className="page-back text-muted" to="/login">
                                            <span><i className="fa fa-angle-left" /></span> Back</Link>
                                        <h3 className="text-center">OTP Verification</h3>
                                        <p className="text-center mb-5">We will send one time code on this number</p>
                                        <form  onSubmit={this.onSubmitHandler}>
                                            <div className="form-group">
                                                <label>Your phone number</label>
                                                <div className="">
                                                    <div className="input-group-prepend">
                                                        <span className="input-group-text pl-4 pr-4"><i className="fa fa-phone" /></span>
                                                    </div>
                                                    <input type="text" className="form-control text-center font-weight-bold" defaultValue="+1 12365480" />
                                                </div>
                                            </div>
                                            <div className="text-center mt-4">
                                                <button type="submit" className="btn btn-success btn-block">Send</button>
                                            </div>
                                        </form>
                                        <div className="new-account mt-3 d-flex justify-content-between">
                                            <p>Don't get code? <Link className="text-primary" to="/otp1">Resend</Link></p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}

export default opt_1
