import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class AccountSetting extends Component {
    render() {
        return (
            <div>
               <div className="content-body">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-xl-3 col-md-4">
                        <div className="card settings_menu">
                            <div className="card-header">
                                <h4 className="card-title">Settings</h4>
                            </div>
                            <div className="card-body">
                                <ul>
                                    <li className="nav-item">
                                        <Link to="/settings" className="nav-link">
                                            <i className="la la-user"></i>
                                            <span>Update Profile</span>
                                        </Link>
                                    </li>
                                    
                                    <li className="nav-item">
                                        <Link to="/changePassword" className="nav-link">
                                            <i className="la la-lock"></i>
                                            <span>Change Password</span>
                                        </Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link to="/accountSetting" className="nav-link active">
                                            <i className="la la-university"></i>
                                            <span> Link Bank Account</span>
                                        </Link>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    
                    <div className="col-xl-9 col-md-8">
                        <div className="card">
                            <div className="card-header">
                                <h4 className="card-title">Add Bank Account</h4>
                            </div>
                            <div className="card-body">
                            <div className="card-body py-0">
            <div className="add-bank-card">
              <div className="row my-4">
                <div className="col-md-12">
                  <Link to="/addBank" className="d-block">
                    <div className="media">
                      <span className="mr-3"><i className="fa fa-bank" /></span>
                      <div className="media-body">
                        <h4 className="mt-0 mb-3">Add Bank Account</h4>
                        <p>Use bank account to make purchase and sells.<br/> Prices are
                          locked today. Trades may take 4-5 days to process</p>
                        <p className="text-muted">Recommended for invest large amount</p>
                      </div>
                    </div>
                  </Link>
                </div>
              
              </div>
            </div>
          </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </div>
        )

    }
}

export default AccountSetting
