import React, { Component } from 'react';
import AuthService from "../Services/auth.service";
import { Link } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";


const required = value => {
    if (!value) {
      return (
        <div className="alert alert-danger" role="alert">
          This field is required!
        </div>
      );
    }
  };
const vpassword = value => {
    if (value.length < 6 || value.length > 40) {
      return (
        <div className="alert alert-danger" role="alert">
         The Password must be at least 6 characters long..
        </div>
      );
    }
  };
  
  const conpass = val => {
   
    var password = document.getElementById("password1").value;
          var confirmPassword = document.getElementById("password2").value;
          if (password !== confirmPassword) {
            return (
              <div className="alert alert-danger" role="alert">
                Passwords do not match.   
              </div>
            );
          }
          
  };



export class ChangePassword extends Component {
    

        constructor(props) {
            super(props);
            this.handleUpdate = this.handleUpdate.bind(this);        
            this.onChangeCurrent_password = this.onChangeCurrent_password.bind(this);
            this.onChangeNew_password = this.onChangeNew_password.bind(this);
            this.onChangeNew_confirm_password = this.onChangeNew_confirm_password.bind(this);
            
           // const userProfile = JSON.parse((localStorage.getItem('users'))).data;  
            this.state = {
            current_password:"",
            new_password:"",
            new_confirm_password: "",
            loading: false,
            successful: false,
            message: ""
            };
          }
        
          onChangeCurrent_password(e) {
            this.setState({
                current_password: e.target.value
            });
          }
          onChangeNew_password(e) {
            this.setState({
                new_password: e.target.value
            });
          }
        
          onChangeNew_confirm_password(e) {
          this.setState({
            new_confirm_password: e.target.value
          });
        }
        
        
        handleUpdate(e) {
          e.preventDefault();
        
          this.setState({
            loading:true,
            message: "",
            successful: false
           });
        
           this.form.validateAll();

          if (this.checkBtn.context._errors.length === 0) {
          const regmessage = "Password Changed Successfully!!!";
            AuthService.postUpdatePassword(    
              this.state.current_password,
              this.state.new_password,
              this.state.new_confirm_password
             
        
            ).then(
              response => {
                  
                this.setState({
                  message:regmessage,
                  successful: true,
                  loading:true
                  
                });
                window.location.reload();
              },
              error => {
                const resMessage =
                  (error.response &&
                    error.response.data &&
                    error.response.data.message) ||
                  error.message ||
                  error.toString();
        
                this.setState({
                 successful: false,
                 message: resMessage,
                 loading:false
                });
              }
            );
          }
}             
  
      


render() {
        return (
            <div>
    <div className="content-body">
    <div className="container-fluid">
      <div className="row">
        <div className="col-xl-3 col-md-4">
          <div className="card settings_menu">
            <div className="card-header">
              <h4 className="card-title">Settings</h4>
            </div>
            <div className="card-body">
              <ul>
                <li className="nav-item">
                  <Link to="/settings" className="nav-link active">
                    <i className="la la-user" />
                    <span>Update Profile</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/changePassword" className="nav-link">
                    <i className="la la-lock" />
                    <span>Change Password</span>
                  </Link>
                </li>
                <li className="nav-item">
                  <Link to="/accountSetting" className="nav-link">
                    <i className="la la-university" />
                    <span>Link Bank Account</span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="col-xl-9 col-md-8">
          <div className="row">
            
            <div className="col-xl-12">
              <div className="card">
                <div className="card-header">
                  <h4 className="card-title">Change Password</h4>
                </div>
                <div className="card-body">

                  <Form onSubmit={this.handleUpdate}  ref={c => { this.form = c; }} >
                  <div className="form-group col-xl-6" >
                  <label htmlFor="password"> Current Password</label>
                  <Input type="password" className="form-control" id="current_password" name="current_password" value={this.state.current_password}
                  onChange={this.onChangeCurrent_password} validations={[required,vpassword]} placeholder="Enter Password"/>
                </div>

                      <div className="form-group col-xl-6">
                  <label htmlFor="password"> New Password</label>
                  <Input type="password" className="form-control" id="password1" name="new_password" value={this.state.new_password}
                  onChange={this.onChangeNew_password} validations={[required,vpassword]} placeholder="Enter Password"/>
                </div>
                

                <div className="form-group col-xl-6">
                  <label htmlFor="c_password"> Confirm New Password</label>
                 <Input type="password" className="form-control" name="c_password" id="password2" value={this.state.new_confirm_password}
                   onChange={this.onChangeNew_confirm_password} validations={[required, vpassword, conpass]} placeholder="Re-enter Password"/>
                 </div>
                     
                      
                      
                   
                    <div className="row">
                      <div className="form-group col-2">
                        <button className="btn btn-success pl-5 pr-5" disabled={this.state.loading}>
                           {this.state.loading && (
                  <span className="spinner-border spinner-border-sm"></span>)}
                   <span>Submit</span></button>
                        
                      </div>
                     
                      </div>
                {this.state.message && (
              <div className="form-group">
                <div className={ this.state.successful ? "alert alert-success" : "alert alert-danger"}
                  role="alert"> {this.state.message}
                </div>
              </div>)}
              <CheckButton style={{ display: "none" }} ref={c => { this.checkBtn = c; }}/>
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>














             </div>
								
					</div>


           
        )
    }
}

export default ChangePassword
