import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import AuthService from "../Services/auth.service";

const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
       Enter your password
      </div>
    );
  }
};

const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        Enter valid email.
      </div>
    );
  }
};

export class Login extends Component {
 constructor(props) {
    super(props);
    AuthService.logout();
    this.handleLogin = this.handleLogin.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.state = {
      email: "",
      password: "",
      loading: false,
      message: ""
    };
  }
 onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }
 onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  handleLogin(e) {
    e.preventDefault();

    this.setState({
      message: "",
      loading: true
    });
    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
     
      AuthService.login(this.state.email, this.state.password).then((result) => {
        AuthService.getProfile().then(result => {
          this.props.history.push("/dashboard");
          window.location.reload();
        }).catch(err => {
          console.log(err);
        });
      },
        error => {
          const loginmessage = "Incorrect Email or Password. Try Again";
          // const resMessage =
          //   (error.response &&
          //     error.response.data &&
          //     error.response.data.message) ||
          //   error.message ||
          //   error.toString();

          this.setState({
            loading: false,
            message: loginmessage,
            email: "",
            password: ""
          });
        }
      );
    } else {
      this.setState({
        loading: false
      });
    }
  }

  render() {
    return (
      <div>
        <div className="container">
          <div className="row justify-content-center  align-items-center">
            <div className="col-xl-5 col-md-6">
              <div className="mini-logo text-center my-5">
                <Link to="/"> <img src="assets/images/blogo.jpg"
                  alt="" style={{ width: "200px" }} /></Link>
                {/* <Link className="page-back text-muted" to="/">
            <span><i className="fa fa-angle-left" /></span>Back Home</Link> */}
              </div>
              <div className="auth-form card">
                <div className="card-header justify-content-center">
                  <h4 className="card-title">Sign in</h4>
                </div>
                <div className="card-body">
                  <Form onSubmit={this.handleLogin}
                    ref={c => { this.form = c; }}>
                    <div className="signin_validate">
                      <div className="form-group">
                        <label>Email</label>
                        <Input type="email" className="form-control" placeholder="hello@example.com"
                          name="email" value={this.state.email}
                          onChange={this.onChangeEmail}
                          validations={[email]} />
                      </div>
                      <div className="form-group">
                        <label>Password</label>
                        <Input type="password" className="form-control" placeholder="Password"
                          name="password" value={this.state.password}
                          onChange={this.onChangePassword}
                          validations={[required]} />
                      </div>
                      <div className="form-row d-flex justify-content-between mt-4 mb-2">
                        <div className="form-group mb-0">
                          <label className="toggle">
                            <input className="toggle-checkbox" type="checkbox" />
                            <span className="toggle-switch" />
                            <span className="toggle-label">Remember me</span>
                          </label>
                        </div>
                        <div className="form-group mb-0">
                          <Link to="/reset">Forgot Password?</Link>
                        </div>
                      </div>
                      <div className="text-center">
                        <button className="btn btn-success btn-block" disabled={this.state.loading} >
                          {this.state.loading && (
                            <span className="spinner-border spinner-border-sm"></span>)}
                          <span>Sign in</span>
                        </button>
                        {this.state.message && (
                          <div className="form-group">
                            <div className="alert alert-danger" role="alert">
                                  {this.state.message}
                            </div>
                          </div>)}
                        <CheckButton style={{ display: "none" }}ref={c => {
                                this.checkBtn = c;
                          }}/>
                      </div>
                    </div>
                    {/* <div className="cen">
                      <p>Or</p>
                      <hr />
                      <p>Sign With</p>

                      <div className="google-btn">
                        <div className="google-icon-wrapper">

                          <div className="google-icon" ><i className="fa fa-google" style={{ color: "red", fontSize: "25px" }} aria-hidden="true"></i></div>
                        </div>
                        <p className="btn-text "><b>Sign in with google</b></p>
                      </div>
                    </div> */}
                  </Form>
                  <div className="new-account mt-3">
                    <p>Don't have an account? <Link to="/register" className="text-primary">Sign up</Link></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>


    )
  }
}

export default Login
