import React, { Component } from 'react'

export class Contact extends Component {
    render() {
        return (
            <div>
              <div className="contact-form section-padding">
  <div className="container">
    <div className="row justify-content-center">
      <div className="col-xl-6">
        <div className="section-title">
          <h2>Let us hear from you directly!</h2>
          <p>We always want to hear from you! Let us know how we can best help you and we'll do our
            very best.
          </p>
        </div>
      </div>
    </div>
    <div className="row">
      <div className="col-xl-4">
        <div className="info-list">
          <h5 className="mb-3">Address</h5>
          <ul>
            <li><i className="fa fa-map-marker" />Lagos, Nigeria</li>
            <li><i className="fa fa-phone" /> (+234) 81843 6666</li>
            <li><i className="fa fa-envelope" /> blinkinfo@gmail.com</li>
          </ul>
        </div>
      </div>
      <div className="col-xl-8">
        <form>
          <div className="row">
            <div className="col-12 col-md-6">
              <div className="form-group mb-5">
                <label htmlFor="contactName">
                  Full name
                </label>
                <input type="text" className="form-control" id="contactName" placeholder="Full name"  required/>
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group mb-5">
                <label htmlFor="contactEmail">
                  Email
                </label>
                {/* Input */}
                <input type="email" className="form-control" id="contactEmail" placeholder="hello@domain.com" required />
              </div>
            </div>
          </div>
           {/* / .row */}
          <div className="row">
            <div className="col-12">
              <div className="form-group mb-7 mb-md-9">
                {/* {/* Label */}
                <label htmlFor="contactMessage">
                  What can we help you with?
                </label>
              {/* Input */} 
                <textarea className="form-control" id="contactMessage" rows={5} placeholder="Tell us what we can help you with!" defaultValue={""} />
              </div>
            </div>
          </div>
          {/* / .row */}
          <div className="row justify-content-center">
            <div className="col-auto">
              {/* Submit */}
              <button type="submit" className="btn btn-primary lift">
                Send message
              </button>
            </div>
          </div>
          {/* / .row */}
        </form>
      </div>
    </div>
  </div>
</div>


            </div>
        )
    }
}

export default Contact
